<template>
    <div class="document">
        <Canvas :document="document"/>

        <va-navbar class="nav"
            style="background: linear-gradient(var(--va-background) 60%, transparent 100%)">
            <template #left>
                <va-navbar-item>
                    <va-button flat icon="arrow_back" text-color="dark"
                        :to="{name: 'home'}">
                        Back
                    </va-button>

                    <va-button flat icon="undo" text-color="dark"
                        :disabled="document.undoStack.length === 0"
                        @click="document.undo()"/>
                    <va-button flat icon="redo" text-color="dark" 
                        :disabled="document.redoStack.length === 0"
                        @click="document.redo()"/>
                </va-navbar-item>
            </template>
            <template #center>
                <b style="color: var(--va-dark)">{{ state.context }}</b>
            </template>
            <template #right>
                <va-navbar-item>
                    <va-popover v-for="user in users" :key="user.id"
                        :message="user.id === document.localUser.id ? `${user.name} (Me)` : user.name"
                        color="gray">
                        <va-avatar :size="37" style="margin-right: 0.5rem;">
                            <Avatar class="avatar" variant="beam" :name="user.name" />
                        </va-avatar>
                    </va-popover>
                </va-navbar-item>
                <va-navbar-item>
                    <va-button gradient style="margin-right: 0.5rem;">
                        Copy Link
                    </va-button>
                    <va-button outline>
                        Download
                    </va-button>
                </va-navbar-item>
            </template>
        </va-navbar>

        <va-modal v-model="loading" :hide-default-actions="true">
            <slot>
                <va-icon name="loop" spin="counter-clockwise"/>
                Syncing document...
            </slot>
        </va-modal>
        <!-- <Menu /> -->
    </div>
</template>

<script>
import { defineComponent, computed } from "vue";
import { VaNavbar, VaNavbarItem, VaButton, VaAvatar, VaPopover, VaModal, VaIcon } from 'vuestic-ui';
import Avatar from "vue-boring-avatars";

import getRandomFruitsName from "random-fruits-name";

import Canvas from "@/components/canvas/Canvas.vue";

import { IntrigueDocument } from "@/store";
import { intrigueMachine } from "@/state";
import { useMachine } from "@xstate/vue";

export default defineComponent({
    name: "DocumentView",
    components: {
        VaNavbar,
        VaNavbarItem,
        VaButton,
        VaAvatar,
        VaPopover,
        VaModal,
        VaIcon,
        Avatar,
        Canvas,
        // VueInfiniteViewer,
        // Moveable
    },

    data() {
        let document;
        if (Array.isArray(this.$route.params.key)) {
            document = new IntrigueDocument(this.$route.params.key[0]);
        } else {
            document = new IntrigueDocument(this.$route.params.key);
        }
        return {
            document: document,
            store: document.store,
            users: [],
            loading: true,
            title: ''
        };
    },

    provide() {
        // use function syntax so that we can access `this`
        const injections = {
            document: this.document,
            store: computed(() => this.document.store),
            state: computed(() => this.state),
            send: this.send,
        }

        for (let key of Object.keys(this.state.context)) {
            injections[key] = computed(() => this.state.context[key]);
        }

        return injections;
    },

    setup() {
        const { state, send } = useMachine(intrigueMachine);
        return { state, send };
    },

    methods: {
        broadcastUsername() {
            const fruit = getRandomFruitsName("en", { maxWords: 1 });
            this.document.updateAwareness("name", `Anonymous ${fruit}`);
        },

        onPinch (event) {
            console.log(event.zoom);
        },
    },

    mounted() {
        this.document.on('synced', () => {
            this.broadcastUsername();
            this.loading = false;
            if (this.$route.params.title) {
                this.store.metadata.name = this.$route.params.title;
                this.title = this.$route.params.title;
            } else {
                this.title = this.store.metadata.name;
            }
        })
    },

    beforeUnmount () {
        console.log('Closing document...');
        this.document.close();
        console.log('Document closed.');
    },

    watch: {
        'document.users': {
            deep: true,
            handler () {
                const users = [];
                for (let userId of Object.keys(this.document.users)) {
                    users.push(this.document.users[userId]);
                }
                this.users = users;
            }
        },
        'document.metadata.name': {
            deep: true,
            handler () {
                this.title = this.document.metadata.name;
            }
        }
    }
});
</script>


