import { createNanoEvents } from 'nanoevents'

export class EventEmitter {
    
    constructor() {
        this.emitter = createNanoEvents();
    }

    // eslint-disable-next-line
    on(event, callback) {
        return this.emitter.on(event, callback);
    }

    emit(event, data) {
        this.emitter.emit(event, data);
    }
}